import React, { memo, useCallback } from "react";

import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { useQueryString } from "../hooks/useQueryString";

const TopContainer = styled.div`
    form {
        background-color: #fff;
        display: flex;
        align-content: center;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
        padding: 10px 0;
        margin: 0;
        margin-bottom: 20px;

        input, button {
            display: block;
            margin-right: 5px;
            font-size: 16px;
            padding: 5px 10px;
        }

        input {
            flex: none;
        }

        button {
            width: 70px;
            flex: none;
        }
    }
`;

const Top = memo(() => {
    /**      */
    //  , queryString       .
    const navigate = useNavigate();

    /** QueryString   */
    const { sd, ed, filter } = useQueryString();
    console.group("Top.js");
    console.log(`sd=${sd}, ed=${ed}, filter=${filter}`);
    console.groupEnd();

    /**  submit  */
    const onSearchSubmit = useCallback((e) => {
        e.preventDefault();

        //     -->  <form> 
        const current = e.currentTarget;

        // <form>   <input> <input>  name  
        const sd = current.startDate.value;
        const ed = current.endDate.value;
        const filter = current.filter.value;
        console.log(": %s, : %s, filter", sd, ed, filter);

        //  
        navigate(`/?filter=${filter}&sd=${sd}&ed=${ed}`);
    }, []);

    return (
        <TopContainer>
            <h1>Covid19 </h1>

            <form onSubmit={onSearchSubmit}>
                <input type="hidden" name="filter" defaultValue={filter} />
                <input type="date" name="startDate" defaultValue={sd} />
                ~
                <input type="date" name="endDate" defaultValue={ed} />
                <button type="submit"></button>
            </form>
        </TopContainer>
    );
});

export default Top;